<template>
    <panel :config="config" class="buddies-collection" :style="{height: displayHeight}">
        <template slot="header">{{ title }}</template>
        <template slot="body" :style="{height: bodyHeight}">
            <vue-word-cloud
                v-if="collection && collection.length > 0"
                :words="words"
                :color="([, weight]) => wordColour(weight)"
                :animation-duration="0"
                font-family="Helvetica Neue"
                />
            <p v-else>
                {{ getTrans('nobody') }}
            </p>
        </template>
    </panel>
</template>

<script>
// TODO check webpack handles common chunks correctly so that we can safely
// remove the following import
// import store from '@/store';
import axios from 'axios';
import HTTP from '@/httpapi';
import GenericMixin from '@mixins/generic';
import Games from '@api/games';
import User from '@api/user';
import VueWordCloud from 'vuewordcloud';

export default {
    name: 'buddies_collection',

    mixins: [GenericMixin],

    components: {
        [VueWordCloud.name]: VueWordCloud
    },

    methods: {
        getBuddiesCollection () {
            let that = this;
            axios.all([
                Games.all(),
                Games.rankables(),
                User.buddiesCollection()
            ]).then(axios.spread(function (games, rankables, collection) {
                that.rankables = rankables.data.rankingGames;
                that.games = games.data.games;
                let accum = [];
                collection.data.collection.forEach(function (entry) {
                    entry.games.forEach(function (gid) {
                        accum.push({'game_id': gid, 'count': entry.count});
                        that.words.push([that.games[gid].name, entry.count]);
                    });
                });
                that.collection = accum;
            }));
        },

        isRankable (gameId) {
            return (this.rankables && this.rankables.findIndex(function (gid) { return gid === gameId; }) >= 0);
        },

        isAValidGame (gameId) {
            return (this.games && typeof this.games[gameId] !== 'undefined');
        },

        wordColour (weight) {
            let range = this.maxWeight - this.minWeight;
            let idx = range === 0 ? 0 : Math.floor((weight - this.minWeight) / range);

            return this.colours[idx * (this.colours.length - 1)];
        }
    },

    computed: {
        displayHeight () {
            return (this.config.bodyHeight && this.collection && this.collection.length > 0 ? (parseInt(this.config.bodyHeight) + 40) + 'px' : '100px');
        },

        maxWeight () {
            return this.collection[0].count;
        },
        minWeight () {
            return this.collection[this.collection.length - 1].count;
        },
    },

    data () {
        return {
            collection: null,
            rankables: null,
            games: null,
            words: [],
            colours: ['#55387c', '#4b2a6a', '#411d58', '#370f46', '#2d0235'], // 5 shades of purple
        };
    },

    mounted: function () {
        HTTP.ifInitialized(this.getBuddiesCollection);
    }
};

</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';

.panel.buddies-collection /deep/ {
    .panel-heading {
        border-bottom: none;
        padding: 15px 15px 0;
        h1 {
            font-size: 1.5em;
            border-bottom: none;
            padding: 0;
        }
    }
    .panel-body {
        padding: 0 15px 15px;
    }

    .row {
        margin-bottom: 10px;
    }

    div[class*='col-'] {
        text-align: center;
        padding: 0;
    }

    a.clickable {
        background-color: $panel-background-color;
    }
}
</style>



